.navbar {
  height: 80px;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  display: flex;
  box-shadow: 0 2px 5px #0000001a;
}

.navbar-container {
  width: 100%;
  max-width: auto;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

.navbar-logo {
  color: #333;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
}

.navbar-links {
  list-style: none;
  display: flex;
}

.navbar-links li:not(:last-child) {
  margin-right: 20px;
}

.navbar-links a {
  color: #333;
  text-decoration: none;
  transition: all .3s;
}

.navbar-links a:hover {
  color: #f1356d;
}

.product-page {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.product {
  border: 1px solid #ccc;
  border-radius: 10px;
  align-items: center;
  margin: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 5px #0000001a;
}

.product-image {
  width: 200px;
  height: 200px;
  object-fit: cover;
  margin-right: 20px;
}

.product-info {
  flex-direction: column;
  display: flex;
}

.product-name {
  margin: 0 0 10px;
  font-size: 1.5rem;
}

.product-award {
  color: #999;
  margin: 0 0 10px;
  font-size: 1rem;
}

.product-inventory {
  margin: 0 0 10px;
  font-size: 1rem;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #f1356d;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  font-size: 1rem;
}

button:hover {
  background-color: #ff4d87;
}

.shopping-cart {
  flex-direction: column;
  align-items: center;
  margin: 2rem;
  display: flex;
}

.cart-item {
  font: bold;
  flex-direction: column;
  flex: 1;
  display: flex;
}

* {
  margin: 0;
  padding: 0;
}

html {
  font-family: Arial, Helvetica, sans-serif;
}

